import h from './Posts.module.css'

const Messages = (props) =>{
return(
  <div className={h.ms}>
      {props.messages}
  </div>
);
}


const ImgToPosts = () => {
    return(
        <div>
              <img src='data:image/jpeg;base64,' />
        </div>
    )
}
const Posts = (props) =>{
    let newMessages = props.messagesDataHome.map( item => <Messages  messages={item.posts}/>)
  
    return(
    <div className={h.wrapper}>
         <ImgToPosts />
       <div className={h.posts}>
             {/* <Messages  messages={messagesData[0].posts}/>
             <Messages  messages={messagesData[1].posts}/>
             <Messages  messages={messagesData[2].posts}/>
             <Messages  messages={messagesData[3].posts}/> */}
             {newMessages}
             
      </div> 
    </div>
    );
}
export default Posts;
